@import url('https://fonts.googleapis.com/css?family=Lato:100|Open+Sans');
.header {
    width: 100%;
    padding: 75px 0px;
    text-align: center;
    vertical-align: middle;
    background: rgb(126,19,163);
    background: -webkit-linear-gradient(45deg, rgba(126,19,163,1) 0%,rgba(242,155,14,1) 100%);
    background: -o-linear-gradient(45deg, rgba(126,19,163,1) 0%,rgba(242,155,14,1) 100%);
    background: linear-gradient(45deg, rgba(126,19,163,1) 0%,rgba(242,155,14,1) 100%); 
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7e13a3', endColorstr='#f29b0e',GradientType=1 );
}
.header-title {
    font-family: 'Lato', sans-serif;
    font-weight: 100;
    text-transform: uppercase;
    color:white;
}
.list-container {
    max-width: 768px;
    padding: 50px 15px;
    margin: 0 auto;
}
#text-input {
    padding:5px;
    border: none;
    border-bottom: 4px solid rgba(126,19,163,1);
    -webkit-transition: all .4s ease;
    -o-transition: all .4s ease;
    transition: all .4s ease;
    width:100%;
    max-width: 500px;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    position: relative;
}

#text-input:focus {
    outline: none;
    border-bottom: 4px solid rgba(242,155,14,1);
}

.submit {
    display: inline-block;
    margin-left: 30px;
    margin-top: 30px;
    color: rgba(126,19,163,1);
    -webkit-transition: all .4s ease;
    -o-transition: all .4s ease;
    transition: all .4s ease;
    font-size: 20px;
}
.submit:hover {
    color: rgba(242,155,14,1);
    cursor: pointer;
}
.item-list {
    list-style: none;
    padding: 0;
    margin-top:150px;
    width: 100%;
}
.item {
    -webkit-box-shadow: 0px 2px 2px 0px rgba(0,0,0,0.2);
    box-shadow: 0px 2px 2px 0px rgba(0,0,0,0.2);

    background: #F4F4F4;
    font-size: 16px;
    padding: 7px 15px;
    border-radius: 3px;
    margin-bottom: 15px;

}

.input-container {
    height:100%;
    float:left;
}

input[type=checkbox] {

    -webkit-appearance: none;

       -moz-appearance: none;

            appearance: none;
    width: 40px;
    height: 40px;
    position:relative;
    outline: none;
    vertical-align: middle;
    clear:both;
}
.item-text{
    padding-left:25px;
}

input[type=checkbox]::after{
    content:"";
    display: inline-block;
    position: absolute;
    left:-25%;
    top:-25%;
    width:100%;
    height:100%;
    background: url('img/check-circle.svg') no-repeat;
    background-size: 100%;
}
input[type=checkbox]:checked {
    outline: none;
}



